.typography {
    font-size: 13px;
    line-height: 19px;
    .h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
        margin: 35px 0 20px;
        font-family: inherit;
        font-weight: 700;
        line-height: 1.2;
        color: var(--Wedgewood);
        text-transform: uppercase;
    }

    h1 {
        font-size: 25px;
    }

    h2 {
        font-size: 21px;
    }

    h3 {
        font-size: 17px;
    }

    h4 {
        font-size: 15px;
    }

    h5 {
        font-size: 14px;
    }
    
    h6 {
        font-size: 14px;
    }

    p {
        margin-top: 20px;
        &:not(:last-child) {
            margin-bottom: 20px;
        }
        &:first-child {
            margin-top: 0;
        }
    }

    a {
        display: inline;
        border-bottom: 1px solid var(--Wedgewood);
        text-decoration: none;
        color: var(--Wedgewood);
    }

    mark {
        padding: .2em;
        background-color: #fcf8e3;
    }

    small {
        font-size: 80%;
        font-weight: 400;
    }

    b, strong {
        font-weight: 800;
    }

    blockquote {
        position: relative;
        margin: 40px 0;
        padding: 30px 0 30px 50px;
        margin-left: 132px;
        border-left: 3px solid var(--Gainsboro);
        font-style: italic;
        &:before {
            position: absolute;
            left: -90px;
            top: calc(50% - 10px);
            content: 'â€œ';
            font-family: "OpenSans";
            font-size: 80px;
            color: var(--Gainsboro);
            font-style: normal;
            line-height: 0.7;
        }
    }

    dl, ol, ul {
        margin: 0;
        padding: 0;
    }

    ol ol, ol ul, ul ol, ul ul {
        margin-bottom: 0;
    }

    ul {
        list-style-position: inside;
        list-style: none;
        li:not(:last-child) {
            margin-bottom: 25px;
        }
        li {
            display: flex;
            align-items: flex-start;
            &:before {
                content: "";
                display: block;
                height: 5px;
                width: 5px;
                border-radius: 50%;
                background-color: var(--mainColor);
                margin: 6.5px 18px 0 0;
                flex-shrink: 0;
            }
            span {
                display: block;
                flex-shrink: 1;
            }
        }
    }
    ol {
        counter-reset: counter;
        li:not(:last-child) {
            margin-bottom: 25px;
        }
        li {
            display: flex;
            counter-increment: counter;
            &:before {
                content: counter(counter) ". ";
                margin-right: 18px;
                font-weight: bold;
            }
            span {
                display: block;
                flex-shrink: 1;
            }
        }
    }

    table {
        border-collapse: collapse;
    }
    img {
        width: 472px;
    }
    img[align="right"] {
        margin: 5px 0 5px 40px;
    }
    img[align="left"] {
        margin: 5px 40px 5px 0;
    }
}
@media(max-width: 900px) {
    .typography {
        .h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
            margin: 25px 0 15px 0;
        }
        p {
            margin-top: 20px;
            &:not(:last-child) {
                margin-bottom: 20px;
            }
        }
        ul, ol {
            li:before {
                margin-right: 10px;
            }
            li:not(:last-child) {
                margin-bottom: 20px;
            }
        }
        blockquote {
            margin: 30px 0 30px 98px;
            padding: 15px 0 15px 30px;
            &:before {
                left: -70px;
            }
        }
        img {
            width: 356px;
            height: auto;
        }
        img[align="right"] {
            margin: 0 0 0 20px;
        }
        img[align="left"] {
            margin: 0 20px 0 0;
        }
    }
}
@media(max-width: 575px){
    .typography{
        h2 {
            font-size: 20px;
        }
        p{
            margin-top: 17px;
            font-size: 14px;
            &:not(:last-child) {
                margin-bottom: 17px;
            }
        }
        ul, ol {
            li:not(:last-child) {
                margin-bottom: 17px;
            }
        }
        blockquote {
            padding-left: 20px;
            margin-left: 70px;
            &:before {
                left: -55px;
            }
        }
        img {
            width: 100%;
        }
        img[align="right"], img[align="left"] {
            margin: 0 0 17px 0;
        }
    }
}